@import url(https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&display=swap);

.App{height:100vh;width:100vw;margin:0 auto;font-family:'Nanum Brush Script', cursive;background:url(/rick_and_morty/static/media/rick-and-morty-background.8682a8fe.jpg);background-size:cover;background-repeat:no-repeat}.find-person{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.find-person__input{width:300px;height:20px;padding:10px;margin-top:20px;background-color:rgba(93,124,14,0.884);border:1px solid rgba(57,77,7,0.884);border-radius:5px;font-size:1.4rem;font-family:'Nanum Brush Script', cursive}.find-person__input:hover{background-color:rgba(93,124,14,0.705)}.find-person__input:active{background-color:rgba(93,124,14,0.555)}.find-person__input::-webkit-input-placeholder{font-size:1.5rem;color:rgba(24,32,2,0.884);font-family:'Nanum Brush Script', cursive}.find-person__input:-ms-input-placeholder{font-size:1.5rem;color:rgba(24,32,2,0.884);font-family:'Nanum Brush Script', cursive}.find-person__input::placeholder{font-size:1.5rem;color:rgba(24,32,2,0.884);font-family:'Nanum Brush Script', cursive}

.card-list{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;padding:30px;grid-gap:20px;gap:20px}.pagination{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin:0 auto;padding:15px;width:700px;grid-gap:10px;gap:10px;border-radius:5px;font-size:1.6rem;list-style-type:none;font-weight:bold;background-color:rgba(11,145,33,0.247)}.pagination>*>*{color:#4e4d04;text-decoration:none}

.card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;min-width:200px;max-width:200px;min-height:300px;padding:20px;border:1px solid #f3bc08;border-radius:5px;background-color:rgba(212,160,17,0.719);cursor:pointer}.card:hover{-webkit-transform:translateY(-10px);transform:translateY(-10px);background-color:rgba(148,110,7,0.719);transition:300ms ease-in-out}.card__img{width:100%;border-radius:5px;margin-bottom:7px}.card__name{margin:0;grid-gap:5px;gap:5px;font-size:25px;font-weight:bold;color:#352702;text-decoration:none}.card__text--right{text-align:right}.card__text--left{text-align:left;font-size:0.9em;color:#58491d}

.selected-card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;min-width:200px;max-width:200px;min-height:300px;padding:20px;border:1px solid #f3bc08;border-radius:5px;background-color:rgba(212,160,17,0.719)}.selected-card-description{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;margin:0;padding:50px}.selected-card__img{width:100%;border-radius:5px;margin-bottom:7px}.selected-card__name{display:grid;grid-template-columns:1fr 3fr;-webkit-justify-content:space-between;justify-content:space-between;margin:0;grid-gap:5px;gap:5px;font-size:22px;font-weight:bold;color:#352702;text-decoration:none}.selected-card__text--right{text-align:right}.selected-card__text--left{text-align:left;color:#58491d}.selected-card__button{width:150px;padding:7px 0;margin-top:10px;border:1px solid #f3bc08;border-radius:5px;background-color:rgba(212,160,17,0.918);cursor:pointer;font-family:inherit;font-size:1.2rem;font-weight:bold;transition:200ms ease-in-out}.selected-card__button:hover{background-color:rgba(161,122,14,0.918)}.home{padding:15px 30px;margin-top:40px;border:1px solid #f3bc08;border-radius:5px;background-color:rgba(212,160,17,0.918);cursor:pointer;font-family:inherit;font-size:1.2rem;font-weight:bold;text-transform:uppercase;transition:200ms ease-in-out}.home:hover{-webkit-transform:scale(1.1);transform:scale(1.1);background-color:rgba(161,122,14,0.918)}

.edit-form{position:absolute;background-color:rgba(146,191,32,0.884);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;height:500px;width:600px;padding:50px;grid-gap:15px;gap:15px;border:1px solid rgba(146,191,32,0.932);border-radius:5px}.edit-form__close{width:20px;height:20px;font-size:20px;position:absolute;right:3%;top:3%;cursor:pointer;transition-duration:200ms}.edit-form__close:hover{-webkit-transform:scale(1.3);transform:scale(1.3)}.edit-form__input{width:50%;padding:10px 5px;background-color:rgba(93,124,14,0.884);border:1px solid rgba(57,77,7,0.884);border-radius:5px}.edit-form__input:hover{background-color:rgba(93,124,14,0.705)}.edit-form__input:active{background-color:rgba(93,124,14,0.555)}.edit-form__button{width:150px;padding:7px 0;margin-top:10px;border:1px solid rgba(146,191,32,0.932);border-radius:5px;background-color:rgba(93,124,14,0.74);cursor:pointer;font-weight:bold;font-size:0.8rem;transition:200ms ease-in-out}.edit-form__button:hover{background-color:rgba(64,85,10,0.932)}

